import { Component, OnInit } from '@angular/core';
import { Club } from 'src/app/models/club-data';
import { ClubService } from 'src/app/services/club.service';

@Component({
  selector: 'app-club',
  templateUrl: './club.component.html',
  styleUrls: ['./club.component.css']
})
export class ClubComponent implements OnInit {
  public clubs: Club[];
  public clWinners: Club[];
  public displayedColumns: string[] = ['name', 'country'];

  constructor(private clubSvc: ClubService) { }

  ngOnInit(): void {
    this.clubSvc.loadClubs().subscribe(clubs => {
      this.clubs = clubs;
    });

    this.clubSvc.loadChampionsLeagueWinners().subscribe(clWinners => {
      this.clWinners = clWinners;
    });
  }

}
